<template>
  <div>
    <mu-appbar style="width: 100%;"  class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white;font-size:18px;">运维列表</span>
    </mu-appbar>
    <!-- <mu-appbar style="width: 100%;" color="#4ed995" class="fiexdHeader">
      <div slot="left" color="#3051f1">
        <a href="#/controlList">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">运维列表</span>
      </div>
    </mu-appbar>-->
    <div class="backWhite" style="margin-top:66px;">
      <div class="headerContent" v-for="(item,index) in list" :key="index">
        <div class="flexList">
          <div>
            <span>时间</span>
          </div>
          <div>
            <span>{{item.gmtOperate}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>监控站点</span>
          </div>
          <div>
            <span>{{item.station}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>类型</span>
          </div>
          <div>
            <span>{{item.type}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>维护人</span>
          </div>
          <div>
            <span>{{item.person}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>运维号</span>
          </div>
          <div>
            <span>{{item.operateId}}</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>备注</span>
          </div>
          <div>
            <span>{{item.remark}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="flex">
      <mu-expansion-panel
        :expand="panel === 'panel1'"
        @change="toggle('panel1')"
        style="width:96%;border-radius:10px;padding:0 20px;"
      >
        <div slot="header">站房环境及巡检结果</div>
        <div v-for="(item,index) in list" :key="index">
          <!-- <div class="flexList">
            <div>
              <span>站房环境检查</span>
            </div>
            <div>
              <span>{{item.content.stationAmbient}}</span>
            </div>
          </div>-->
          <div class="flexList">
            <div>
              <span>运行状态</span>
            </div>
            <div>
              <span>{{item.content.appSystemRunState}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>传感器</span>
            </div>
            <div>
              <span>{{item.content.appSystemTemperSensor}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>供电</span>
            </div>
            <div>
              <span>{{item.content.appSystemPower}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>空压机</span>
            </div>
            <div>
              <span>{{item.content.appSystemAirPump}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>温度</span>
            </div>
            <div>
              <span>{{item.content.appSystemTemper}}</span>
              <!-- appSystemIPC -->
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>工控机</span>
            </div>
            <div>
              <span>{{item.content.appSystemIPC}}</span>
              <!-- appSystemIPC -->
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>搅拌电机</span>
            </div>
            <div>
              <span>{{item.content.appSystemStirMotor}}</span>
              <!-- appSystemIPC -->
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>采水系统</span>
            </div>
            <div>
              <span>{{item.content.appSystemWaterExtraction}}</span>
              <!-- appSystemIPC -->
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>站房环境检查</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.stationAmbient}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>巡检内容及结果</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.stationAmbientCnt}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>操作内容</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.stationAmbientRes}}</span>
            </div>
          </div>
        </div>
      </mu-expansion-panel>
    </div>
    <div class="flex">
      <mu-expansion-panel
        :expand="panel === 'panel2'"
        @change="toggle('panel2')"
        style="width:96%;border-radius:10px;padding:0 20px;"
      >
        <div slot="header">其他内容</div>
        <div v-for="(item,index) in list" :key="index">
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>仪器与系统检查</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.appSystem}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>易损更换</span>
            </div>
            <div
              class="flexListContent"
              v-for="(item1,index1) in item.content.vulnerableParts"
              :key="index1"
              style="height:80px;"
            >
              <div>
                <span>名称：</span>
                <span>{{item1.vulnerablePartsCnt}}</span>
              </div>
              <div>
                <span>数量：</span>
                <span>{{item1.vulnerablePartsRes}}</span>
              </div>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>耗材更换</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.consumables}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>耗材更换数量及名称</span>
            </div>
            <div
              class="flexListContent"
              v-for="(item1,index1) in item.content.consumablesGroup"
              :key="index1"
              style="height:80px;"
            >
              <div>
                <span>名称：</span>
                <span>{{item1.consumablesName}}</span>
              </div>
              <div>
                <span>数量：</span>
                <span>{{item1.consumablesNum}}</span>
              </div>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>试剂更换</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.reagent}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>巡检内容及结果</span>
            </div>
            <div>
              <span>{{item.content.reagentRes}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>试剂更换</span>
            </div>

            <div
              class="flexListContent"
              v-for="(item1,index1) in item.content.reagentGroup"
              :key="index1"
              style="height:80px;"
            >
              <div>
                <span>名称：</span>
                <span>{{item1.reagentName}}</span>
              </div>
              <div>
                <span>数量：</span>
                <span>{{item1.reagentNum}}</span>
              </div>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>试剂更换</span>
            </div>
            <div class="flexListContent">
              <span>{{item.content.piping}}</span>
            </div>
          </div>
          <div class="flexList">
            <div>
              <span>巡检内容及结果</span>
            </div>
            <div>
              <span>{{item.content.pipingRes}}</span>
            </div>
          </div>
          <div class="flexList" style="flex-direction:column;">
            <div>
              <span>试剂更换</span>
            </div>

            <div
              class="flexListContent"
              v-for="(item1,index1) in item.content.pipingGroup"
              :key="index1"
              style="height:80px;"
            >
              <div>
                <span>名称：</span>
                <span>{{item1.pipingName}}</span>
              </div>
              <div>
                <span>数量：</span>
                <span>{{item1.pipingNum}}</span>
              </div>
            </div>
          </div>
        </div>
      </mu-expansion-panel>
      <!-- 
        <div class="flexList">
            <div>
              <span>温度</span>
            </div>
            <div>
              <span>{{item.content.appSystemTemper}}</span>
            </div>
          </div>
      -->
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "controlListDetails",
  data() {
    return {
      listContent: [],
      list: [],
      token: "",
      panel: ""
    };
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData() {
      var url = "/job/operate/list";
      var pageNum = 1;
      var pageSize = 100000000;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log("res", res);
      this.listContent = res.data.data.rows;

      for (let index = 0; index < this.listContent.length; index++) {
        const element = this.listContent[index];
        this.queryId(element);
      }
    },
    queryId(e) {
      var id = this.$route.query.name;

      if (e.operateId === id) {
        console.log(this.list);
        return this.list.push(e);
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initData();
  }
};
</script>
<style scoped>
.flexList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px rgb(241, 240, 240) solid;
}
.backWhite {
  background: white;
  margin: 10px;
  border-radius: 10px;
}
.flex {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
}
.flexListContent {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  border: #ccc 1px solid;
  padding: 5px;
  margin-bottom: 5px;
  /* align-items: center;
  align-content: center; */
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
.header {
  position: fixed;
  top: 0;
}
.headerContent {
  padding: 0px 10px 0 10px;
}

</style>



